<template>
	<view class="topic-details-wrap">
		<!-- 模糊背景 -->
		<view class="topic-details-bg">
			<image :src="topicInfo.topicImage" mode="aspectFill" lazy-load></image>
		</view>

		<!-- 话题内容 -->
		<view class="topic-details-info">
			<view class="topic-info-t u-f-ac">
				<image :src="topicInfo.topicImage" mode="widthFix" lazy-load></image>
				<view class="topic-title">#{{topicInfo.title}}#</view>
			</view>
			<view class="topic-info-c u-f-ac">
				<view>
					动态 {{topicInfo.total}}
				</view>
				<view>
					今日 {{topicInfo.dayNum}}
				</view>
			</view>
			<view class="topic-info-b">
				{{topicInfo.desc}}
			</view>
		</view>

		<!-- 相关列表 -->
		<u-sticky offset-top="140">
			<!-- 只能有一个根元素 -->
			<view class="topic-detail-bar">
				<tab-bar-head :tabBars="tabBars" :tabIndex="tabIndex" @tabtap="toggleTab"></tab-bar-head>
			</view>
		</u-sticky>

		<view class="topic-detail-list">

			<view class="list-wrap">
				<template v-if="newsList[tabIndex].list.length">
					<!-- 图文列表 -->
					<view v-for="(item1,index1) in newsList[tabIndex].list" :key="index1">
						<common-list :item="item1"></common-list>
					</view>

					<!-- 上拉加载 -->
					<load-text :loadText="newsList[tabIndex].loadText"></load-text>
				</template>
				<template v-else>
					<!-- 无内容 -->
					<no-thing></no-thing>
				</template>
			</view>
		</view>

		<!-- 提示 -->
		<u-toast ref="uToast" />

	</view>
</template>

<script>
	import TabBarHead from '../../components/index/swiper-tab-head.vue'
	import CommonList from '../../components/common/common-list.vue'
	import LoadText from '../../components/load-more/load-more.vue'
	import NoThing from '../../components/no-thing/no-thing.vue'
	import {
		deepCopy
	} from '../../utils/utils.js'
	export default {
		components: {
			TabBarHead,
			CommonList,
			LoadText,
			NoThing
		},
		data() {
			return {
				topicInfo: {
					topicImage: '../../static/demo/topicpic/13.jpeg',
					title: '这个话题不简单',
					desc: '不简单的描述',
					dayNum: 284,
					total: 23497
				},
				tabIndex: 0,
				tabBars: [{
						name: "关注",
						id: 1
					},
					{
						name: "话题",
						id: 2
					}
				],
				newsList: [{
						loadText: '上拉加载更多',
						loadTimes: 2, //模拟两次后没有数据
						name: '推荐',
						list: [{ //只有文字
								userName: '李二狗',
								userPic: '../../static/demo/userpic/10.jpg',
								sex: 0, //0男  1 女
								age: 20,
								isguanzhu: false,
								title: '仅文字的标题',
								titlePic: "",
								video: false,
								share: false,
								address: '上海市 普陀区',
								shareNum: 20,
								commentNum: 30, //评论数
								goodNum: 40 //点赞数
							},
							{ //图文
								userName: '王小美',
								userPic: '../../static/demo/userpic/11.jpg',
								sex: 1, //0男  1 女
								age: 18,
								isguanzhu: false,
								title: '图文的标题',
								titlePic: "../../static/demo/banner2.jpg",
								video: false,
								share: false,
								address: '北京市 朝阳区',
								shareNum: 20,
								commentNum: 30, //评论数
								goodNum: 40 //点赞数
							},
							{ //视频
								userName: '张大炮',
								userPic: '../../static/demo/userpic/16.jpg',
								sex: 0, //0男  1 女
								age: 40,
								isguanzhu: false,
								title: '视频图文的标题',
								titlePic: "../../static/demo/datapic/1.jpg",
								video: {
									lookNum: "22.2W",
									time: "20.23"
								},
								share: false,
								address: '河南省 洛阳市',
								shareNum: 20,
								commentNum: 30, //评论数
								goodNum: 40 //点赞数
							},
							{ //分享
								userName: '张玫瑰',
								userPic: '../../static/demo/userpic/2.jpg',
								sex: 1, //0男  1 女
								age: 23,
								isguanzhu: false,
								title: '分享的标题',
								titlePic: "",
								video: false,
								share: {
									title: '分享的标题',
									titlePic: '../../static/demo/datapic/12.jpg'
								},
								address: '上海市 普陀区',
								shareNum: 20,
								commentNum: 30, //评论数
								goodNum: 40 //点赞数
							}
						]
					},
					{
						list: []
					}
				]
			}
		},
		onReachBottom() { //上拉加载
			// console.log("页面触底")
			this.loadMore(this.tabIndex)
		},
		onPullDownRefresh() { //下拉刷新
			// console.log("刷新页面")
			setTimeout(() => {
				this.downRefresh();
			}, 3000)
		},
		methods: {
			toggleTab(index) {
				this.tabIndex = index;
			},
			downRefresh() {
				let list = deepCopy([this.newsList[this.tabIndex].list[1], this.newsList[this.tabIndex].list[3], this.newsList[this.tabIndex].list[0], this.newsList[this.tabIndex].list[2]]);

				this.newsList[this.tabIndex].list = list;
				
				uni.stopPullDownRefresh()
				this.$refs.uToast.show({
					title: '为您更新了8条内容',
					type: 'default'
				})
				
			},
			loadMore(index) { //上拉加载
				if (this.newsList[index].loadTimes < 0) {
					this.newsList[index].loadText = '没有更多数据了';
					return
				}
				if (this.newsList[index].loadText !== '上拉加载更多') return
				this.newsList[index].loadText = '正在加载中。。。';

				setTimeout(() => {
					let list = deepCopy([this.newsList[index].list[1], this.newsList[index].list[3], this.newsList[
						index].list[0], this.newsList[index].list[2]]);

					this.newsList[index].list.push(...list);
					// console.log(this.newsList[index].list)
					this.newsList[index].loadText = '上拉加载更多';
					this.newsList[index].loadTimes--;
				}, 1000)

			}
		}
	}
</script>

<style scoped lang="scss">
	.topic-details-wrap {
		width: 100%;
		// height: 100%;

		// display: flex;
		// align-items: flex-start;
		// justify-content: flex-start;
		// flex-direction: column;

		.topic-details-bg {
			width: 100%;
			height: 300upx;
			position: relative;
			overflow: hidden;

			image {
				width: 100%;
				position: absolute;
				filter: blur(20upx);
			}
		}


		.topic-details-info {
			width: 100%;
			position: relative;
			padding: 0 20upx;

			.topic-info-t {
				padding: 20upx 0;

				image {
					width: 150upx;
					height: 150upx;
					border-radius: 25upx;
					position: absolute;
					top: -80upx;

				}

				.topic-title {
					font-size: 36upx;
					margin-left: 180upx;
					flex: 1;
					font-weight: bold;
				}
			}

			.topic-info-c {
				padding-bottom: 10upx;
				font-size: 28upx;
				color: #cdcdcd;
			}

			.topic-info-b {
				padding-bottom: 10upx;
				color: #a3a3a3;
				font-size: 32upx;
			}
		}

		.topic-detail-bar {
			background-color: #fff;
			width: 100%;
			text-align: center;
		}

		.topic-detail-list {
			width: 100%;
			height: 100%;

			// flex: 1;
			.swiper-box {
				height: 100%;
			}
		}
	}
</style>
